import { mapGetters } from 'vuex';
import Alert from './Alert.vue';
import axios from 'axios';
import $http from '@/api';

const SET_ALL_LINKS = 'SET_ALL_LINKS';

export default {
  name: 'Links',
  data() {
    return {
      links: [],
      addLinkForm: {
        original: '',
        short: '',
        friendly: '',
        type_id: '1',
        options: [
          { text: 'Общий доступ', value: '1' },
          { text: 'Публичный', value: '2' },
          { text: 'Приватный', value: '3' }
        ],
      },
      message: '',
      showMessage: false,
      editForm: {
        id: '',
        original: '',
        short: '',
        friendly: '',
        type_id: '1',
        options: [
          { text: 'Общий доступ', value: '1' },
          { text: 'Публичный', value: '2' },
          { text: 'Приватный', value: '3' }
        ],
      }
    };
  },
  components: {
    alert: Alert,
  },
  methods: {
    getLinks() {
      $http.get('/links')
        .then((res) => {
          this.links = res.data;
          console.log(res.data);
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.error(error);
        });
    },
    addLink(payload) {
       $http.post('/add-link', payload)
        .then(() => {
          this.getLinks();
          this.message = 'Ссылка добавлена!';
          this.showMessage = true;
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.log(error);
          this.getLinks();
        });
    },
    initForm() {
      this.addLinkForm.original = '';
      this.addLinkForm.short = '';
      this.addLinkForm.friendly = '';
      this.addLinkForm.type_id = '1';
      this.addLinkForm.options = [
          { text: 'Общий доступ', value: '1' },
          { text: 'Публичный', value: '2' },
          { text: 'Приватный', value: '3' }
        ];
      this.editForm.id = '';
      this.editForm.original = '';
      this.editForm.short = '';
      this.editForm.friendly = '';
      this.editForm.type_id = '1';
      this.editForm.options = [
          { text: 'Общий доступ', value: '1' },
          { text: 'Публичный', value: '2' },
          { text: 'Приватный', value: '3' }
        ];
    },
    onSubmit(evt) {
      evt.preventDefault();
      this.$refs.addLinkModal.hide();
      const payload = {
        original: this.addLinkForm.original,
        short_link: this.addLinkForm.short,
        friendly_link: this.addLinkForm.friendly,
        type_id: this.addLinkForm.type_id,
      };
      this.addLink(payload);
      this.initForm();
    },
    onReset(evt) {
      evt.preventDefault();
      this.$refs.addLinkModal.hide();
      this.initForm();
    },
    editLink(link) {
      this.editForm = link;
      this.editForm.friendly = '';
    },
    onSubmitUpdate(evt) {
      evt.preventDefault();
      this.$refs.editLinkModal.hide();
      const payload = {
        friendly_link: this.editForm.friendly,
        type_id: this.editForm.type_id,
      };
      this.updateLink(payload, this.editForm.id);
    },
    updateLink(payload, linkID) {
      $http.put('/links/'+linkID, payload)
        .then(() => {
          this.getLinks();
          this.message = 'Ссылка обновлена!';
          this.showMessage = true;
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.error(error);
          this.getLinks();
        });
    },
    onResetUpdate(evt) {
      evt.preventDefault();
      this.$refs.editLinkModal.hide();
      this.initForm();
      this.getLinks(); 
    },
    removeLink(linkID) {
      $http.delete('/links/'+linkID)
        .then(() => {
          this.getLinks();
          this.message = 'Ссылка удалена!';
          this.showMessage = true;
        })
        .catch((error) => {
          // eslint-disable-next-line
          console.error(error);
          this.getLinks();
        });
    },
    onDeleteLink(link) {
      this.removeLink(link.id);
    },
  },
  created() {
    this.getLinks();
  },
};